    position: absolute;
    top: 7px;
    width: 15px;
    height: 1px;
    background-color: #c4c4c4;
  }
  .main-footer__accordion-container.is-closed .main-footer__accordion-button::before, .main-footer__accordion-container.is-closed .main-footer__accordion-button::after {
