  .main-footer__accordion-container.is-closed .footer-nav__wrapper,
.main-footer__accordion-container.is-closed .footer-contacts__list {
    display: none;
  }
  .main-footer__accordion-container.is-opened .footer-nav__wrapper,
.main-footer__accordion-container.no-js .footer-nav__wrapper {
    display: flex;
  }
  .main-footer__accordion-container.is-opened .footer-contacts__list,
.main-footer__accordion-container.no-js .footer-contacts__list {
    display: block;
  }
  .main-footer__accordion-container.is-closed .main-footer__title {
    margin-bottom: 0;
  }
}
.socials__list {
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: 40px 40px 40px;
  grid-template-rows: 40px;
  gap: 15px;
  list-style: none;
}

.socials__link {
  fill: #f8fdff;
  color: inherit;
}
.socials__link:hover {
  fill: #00b2ff;
}
.socials__link:focus, .socials__link:focus-within {
  fill: #00b2ff;
  outline: none;
}
.socials__link:active {
  fill: rgba(0, 178, 255, 0.7);
}

.socials__link svg {
  fill: inherit;
}

.footer-nav__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.footer-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer-nav__item {
  margin-bottom: 10px;
}
.footer-nav__item:last-child {
  margin-bottom: 0;
}

.footer-nav__link {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}
.footer-nav__link:hover {
  color: #00b2ff;
}
.footer-nav__link:focus, .footer-nav__link:focus-within {
  color: #00b2ff;
  outline: none;
}
.footer-nav__link:active {
  color: rgba(0, 178, 255, 0.7);
}

@media (max-width: 1023px) {
  .footer-nav__wrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}
@media (max-width: 767px) {
  .footer-nav__link {
    font-size: 14px;
  }
}
.footer-contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer-contacts__item {
  margin-bottom: 10px;
}
.footer-contacts__item:last-child {
  margin-bottom: 0;
}

.footer-contacts__link {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}
.footer-contacts__link--tel {
  line-height: 25px;
}
.footer-contacts__link:hover {
  color: #00b2ff;
}
.footer-contacts__link:focus, .footer-contacts__link:focus-within {
  color: #00b2ff;
  outline: none;
}
.footer-contacts__link:active {
  color: rgba(0, 178, 255, 0.7);
}

@media (max-width: 767px) {
  .footer-contacts__link {
    font-size: 14px;
  }
}
